define('home/homeRoutes',['require','./module','tpl!./mixpo-icons.html','tpl!./navigation.html','tpl!./index.html','tpl!./typography.html','tpl!./button.html','tpl!./table.html','tpl!./dropdown.html','tpl!./checkboxes.html','tpl!./radio-buttons.html','tpl!./multi-select.html','tpl!./tooltip.html','tpl!./nav.html'],function (require) {
    'use strict';
    var app = require('./module');
    require('tpl!./mixpo-icons.html');
    require('tpl!./navigation.html');
    require('tpl!./index.html');
    require('tpl!./typography.html');
    require('tpl!./button.html');
    require('tpl!./table.html');
    require('tpl!./dropdown.html');
    require('tpl!./checkboxes.html');
    require('tpl!./radio-buttons.html');
    require('tpl!./multi-select.html');
    require('tpl!./tooltip.html');
    require('tpl!./nav.html');

    return app.config(['$stateProvider', function ($stateProvider) {
        $stateProvider
            .state('root', {
                url: '/',
                templateUrl: 'home/index.html',
                controller: 'HomeCtrl'
            });
    }]);
});

